<template>
    <div class="area">
        <el-button @click="() => checkPersonArchive('ry')" type="primary">人员档案数据校验</el-button>
        <el-button @click="() => checkPersonArchive('dw')" type="primary">队伍档案数据校验</el-button>
        <el-button @click="() => checkPersonArchive('qy')" type="primary">企业档案数据校验</el-button>
        <div class="warn"><span>{{ warn }}</span></div>
        <el-table
                v-loading="loading"
                :data="tableData"
                stripe
                style="width: 100%">
            <el-table-column
                    type="index"
                    width="50">
            </el-table-column>
            <el-table-column
                    label="描述"
                    prop="desc"
                    width="180">
            </el-table-column>
            <el-table-column
                    label="sql"
                    prop="sql">
            </el-table-column>
        </el-table>
    </div>

</template>

<script>
    export default {
        name: "Demo",
        methods: {
            handleClick(row) {
                console.log(row);
            },
            checkPersonArchive(codeSystemId) {
                this.loading = true;
                this.postRequest('sql/check', '{"codeSystemId":"' + codeSystemId + '"}"').then(resp => {
                    if (resp) {
                        this.loading = false;
                        console.log(resp.data)
                        this.tableData = resp.data;
                        this.warn = resp.msg;
                    } else {
                        this.loading = false;
                        this.tableData = [];
                        this.warn = '接口请求异常！！';
                    }
                })
            },
            showAddDepView() {
                console.log('here111')
                this.tableData = [{
                    no: '1111',
                    desc: '人员档案未增加索引11',
                    sql: 'select * from person11'
                }];
            }
        },
        data() {
            return {
                tableData: [],
                loading: false,
                warn: '添加工资账套'
            }
        }
    }
</script>

<style scoped>
    .area {
        border-radius: 15px;
        margin: 40px auto;
        width: auto;
        padding: 15px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }

    .warn {
        height: 50px;
        width: auto;
        margin: 10px auto;
    }
</style>