<script>
    export default {
        name: 'HelloWorld',
        data() {
            return {
                dataJson: {
                    msg: 'aa'
                }
            }
        },
        props: {
            msg: String
        },
        methods: {
            skip() {
                this.$router.push({path: '/demo1', replace: true})
            },
            showAddDepView() {
                console.log('here')
                this.getRequest("chat/get_bob").then(resp => {
                    if (resp) {
                        console.log(resp)
                    }
                })
            },
            showAddDepViewPost() {
                console.log('post')
                this.postRequest("chat/post_bob", this.dataJson).then(resp => {
                    if (resp) {
                        console.log(resp)
                    }
                })
            },
            showAddDepViewPut() {
                console.log('put')
                this.putRequest("chat/put_bob", this.dataJson).then(resp => {
                    if (resp) {
                        console.log(resp)
                    }
                })
            },
            showAddDepViewDelete() {
                console.log('delete')
                this.deleteRequest("chat/delete_bob").then(resp => {
                    if (resp) {
                        console.log(resp)
                    }
                })
            }
        }
    }
</script>

