import Vue from 'vue'
import Router from 'vue-router'
import Demo from './views/Demo'
import Demo1 from './views/Demo1'
import HelloWorld from "./components/HelloWorld";

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/demo',
            name: 'Demo',
            component: Demo,
            hidden: true
        }, {
            path: '/demo1',
            name: 'Demo1',
            component: Demo1,
            hidden: true
        }, {
            path: '/helloWorld',
            name: 'helloWorld',
            component: HelloWorld,
            hidden: true
        }, {
            path: '*',
            redirect: '/demo'
        }
    ]
})